@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&family=Roboto:wght@300;400;500;700&display=swap);
html {
  font-size: 62.5%;
}

*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  /* font-family: "Lato", sans-serif; */
  font-family: "Roboto", sans-serif;
  /* font-family: "Satoshi", sans-serif; */
  box-sizing: border-box;
  overflow-x: hidden;
}

.about-me,
.portfolio,
.myCv,
.blog,
.contact-me {
  display: flex;
  height: 90vh;
  font-size: 3rem;
}

::selection {
  /* background-color: #5fbba1; */
  background-color: rgb(57, 87, 78);
  color: white;
}

.side-menu {
  background-color: var(--main-bg-color);

  width: 25vw;
  height: 100vh;
  position: fixed;
  /* color: #910d0dcc; */
}

.name-and-photo {
  position: absolute;
  top: 17%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.photo-sketch {
  border-radius: 50%;
  width: 100%;
  margin-top: 5rem;
  margin-bottom: 4rem;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.name-link {
  text-decoration: none;
  padding-top: 0.8rem;
  color: rgb(255, 255, 255);
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  white-space: nowrap;
  letter-spacing: 0.2rem;
  line-height: 3.5rem;
}

.name-link a {
  display: fixed;
  align-items: center;
  justify-content: center;
}

.my-name {
  display: flex;
  justify-content: center;
  font-weight: 500;
  letter-spacing: 2.5px;
  font-size: 2rem;
}

.front-end {
  display: flex;
  justify-content: center;
  font-weight: 200;
  font-size: 1.7rem;
}

.menu-items-link {
  position: absolute;
  width: 80%;
  top: 160%;
  left: 25%;
  transform: translate(-50%, -50%);
  white-space: nowrap;
  line-height: 4.5rem;
}

.menu-items-link a:hover,
.menu-items-link a:focus {
  color: white;
}

.menu-items-link a:hover::after,
.menu-items-link a:focus::after {
  content: "          \27A3";
  transform: scale(1.5);
  transition: all 0.2s ease-in-out;
  color: white;
}

.side-text {
  display: flex;
  font-size: 1.8rem;
  font-weight: 200;
  line-height: 5rem;
}
.side-text a {
  text-decoration: none;
  color: white;
  font-weight: 300;
}
.title-span {
  margin-left: 1.5rem;
}

.get-in-touch {
  font-size: 1.5rem;
  color: rgb(221, 217, 217);

  position: absolute;
  top: 225%;
  left: 20px;
  transform: translate(-50%, -50%);
  white-space: nowrap;
}

.sns-icons {
  /* font-size: 2.7rem;
  position: absolute;
  top: 240%;

  left: 30%;
  transform: translate(-50%, -50%);
  white-space: nowrap; */

  font-size: 2.7rem;
  position: absolute;
  top: 240%;

  left: 22%;
  transform: translate(-50%, -50%);
  white-space: nowrap;
}

.sns-icons a {
  display: inline-block;
  color: rgb(236, 233, 233);
}

.sns-icons a:hover {
  color: white;
  transform: scale(1.3);
  transition: all 0.2s ease-in-out;
}

.social-media-span {
  padding-right: 1rem;
}

.menu-icon {
  display: none;
}
.unshow-mobile-menu-items {
  display: none;
}

@media screen and (max-width: 1365px) and (min-width: 1001px) {
  .my-name {
    font-size: 1.55rem;
  }

  .front-end {
    font-size: 1.45rem;
  }

  .menu-items-link {
    top: 170%;
  }

  .side-text {
    font-size: 1.65rem;
    font-weight: 200;
  }

  .get-in-touch {
    font-size: 1.45rem;
    top: 235%;
    left: 27px;
  }

  .sns-icons {
    font-size: 2.2rem;
    top: 253%;
    left: 20px;
  }
}

