    margin-top: 0.7rem;
  }

  .my-name {
    font-size: 2.3rem;
  }

  .front-end {
    font-size: 1.9rem;
  }

  .menu-icon {
    color: #eee;
    display: flex;
    position: absolute;
    top: 50%;
    right: 5%;
    transform: translate(-50%, -50%);
    font-size: 2.5rem;
  }

  .sns-icons {
    font-size: 2.2rem;
    position: absolute;
    top: 55%;
    left: 55px;
    transform: translate(-50%, -50%);
  }

  .sns-icons a {
    color: #eee;
  }
}

