.skills-container {
  margin-top: 1.5rem;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  /* background: coral; */

  padding-bottom: 2rem;
}

.skills-wrapper {
  position: relative;
  background-color: rgba(238, 238, 238, 0.75);
  margin: 0 auto;
  /* display: grid; */

  padding: 15px;
  justify-content: center;
  align-items: center;
  width: auto;
  box-shadow: 0 1px 3px rgba(85, 85, 85, 0.6);
  /* box-shadow: 0 1px 3px rgba(194, 221, 213, 0.7); */
  border-radius: 20px;
}

.skills-card {
  /* background-color: rgb(255, 255, 255, 0.9); */
  background-color: transparent;
  /* background: #ebeee1;
  border: 1.7px solid #f3e5a5; */
  /* border: 1.7px solid rgba(75, 115, 103, 0.502); */
  border-radius: 10px;
  display: flex;
  flex-direction: row;

  color: var(--secondary-page-color);
  font-weight: bold;
  transition: all 0.2s ease-in;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-items: flex-start;
}

/* .skills-name {
  font-size: 2.5rem;
