  line-height: 1.5;
  word-wrap: break-word;
  margin: 1rem 3rem;
  max-width: 12rem;
  /* background-color: #e98074; */
  width: auto;
  justify-content: flex-start;
  align-items: flex-start;
  /* text-align: center; */
  justify-content: center;
  align-items: center;
}

.styled-components-text {
  white-space: nowrap;
}

.tech-icons {
  font-size: 5.3rem;
  justify-content: center;
