  text-align: center;
  margin: 10px 25px 0 25px;
  font-weight: 600;
  color: rgb(25, 43, 38);

  background-color: var(--secondary-page-color);
  color: white;
}

.contactMeBtn:hover,
.projectsBtn:hover {
  background-color: var(--main-bg-color);
  color: white;
}

